import { Teksten } from "../../../content";
import { DefaultLayout } from "../../layouts";
import {
  FooterOrganism,
  HeaderOrganism,
  HomeImageOrganism,
  MissieVisieWerkwijzenOrganism,
  SloganOrganism,
  TeamSectionOrganism,
} from "../../organisms";

export default function HomeScreen() {
  return (
    <DefaultLayout
      header={<HeaderOrganism />}
      body={
        <>
          <HomeImageOrganism />
          <MissieVisieWerkwijzenOrganism />
          <SloganOrganism slogan={Teksten.slogan1.title} />
          <TeamSectionOrganism />
        </>
      }
      footer={<FooterOrganism />}
    />
  );
}
